.container {
	@include container-fixed;
	//min-width: $container-md;
	width: 100%;

	//tablet, mostly we dont use this but for specific details*/
	@media (min-width: $screen-sm-min) {
		width: $container-sm;
	}
	//medium devices 
	@media (min-width: $screen-md-min) {
		width: $container-md;
	}

	@media (min-width: $screen-lg-min) {
		width: $container-lg;
	}

	@media (min-width: $screen-xlg-min) {
		width: $container-xlg;
	}
}

.container-fluid {
	@include container-fixed;
	//smin-width: $container-md;
	width: 100%;
}

footer {
	@media (min-width: $screen-md-min) { 
		.container-fluid {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
}

/* Extends bootstrap containers */

.container-absolute {

	[data-locked="true"]  & {
		@extend .container;
		position: absolute;
		top: 50%;
		left: 50%;
		@include translate(-50%,-50%);
	}
	@media (min-width: 37.500em){
		@include container-fixed;
		width: 100%;
		position: absolute;
		top: 50%;
		left: 50%;
		@include  translate(-50%,-50%);
	}
}

.container-full {
	@extend .container-fluid; 
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	margin: 0 auto;
	margin-top: - $navbar-height;
}

.container-error {
	@extend .container-fluid; 
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	margin: 0 auto;
	text-align: center;
	
	@media (max-width: $screen-xs-min) {
		padding-top: 70px;

		.btn-lg {
			width: 80%;
		}
	}

	h2 {
		font-size: 33px;
		margin: 35px 0 20px;
    	padding-right: 118px;

    	@media (max-width: $screen-md-min) {
			padding-right: 0px;
    	}
    	
    	@media (max-width: $screen-xs-min) {
			padding: 0 20px;
			text-align: center;
			font-size: 26px;
    		margin-bottom: 30px;
    	}
	}

	@media (min-width: $screen-md-min) {
		text-align: left;

		.btn-lg {
			padding: 10px 78px;
		}
	}

	h2.quote {
		text-align: left;
		margin-top: 100px;

		@media (min-width: $screen-md-min) {
			text-align: right;
			margin-top: 0;
		}
	}

	h4.quote-author {
		text-align: right;
	}

	p.quote-film {
		text-align: right;
		color: $quote-film-color;
		font-weight: bold;
	}

	.container-absolute {
		height: 50%;
	}
}

.container-wrapper {
	@extend .container;
	//@include alpha-attribute('background-color', rgba($dark, 0.7), $light);
	-webkit-box-flex: 1;
    -webkit-flex: 1 0 auto;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
	position: relative;
}

.container-detail {
	@extend .container;
	padding-bottom: 35px;
	position: relative; 
}

.container-detail-mobile {
	@extend .container-detail;
	margin-top: -$navbar-min-height;
}

.container-search {
	@extend .container-wrapper;
	margin-top: -$navbar-height;
	padding-top: $navbar-height;
	background-color: $bg-body;
	width: 100%;

	@media (min-width: $screen-sm-min) {
		padding-top: $navbar-height * 2;
	}
}

.container-grid {
	@extend .container-fluid;
	background-color: $bg-body;
}

.container-landing {
	@extend .container-fluid;
	padding: 0;
	margin-top: -$navbar-min-height;
	overflow-x: hidden;
    overflow-y: auto;
}

@media (min-width: $screen-xlg-min) {
	@include make-grid(xlg);
}

@media (min-width: $screen-mxlg-min) {
	@include make-grid(mxlg);
}

.search-loader {
	//position:absolute;
	width: 100%;
	height: 100%;
	background-color: transparent;
	opacity: 0.8;
	z-index: 999;
	top: 0;
	left: 0;
	//padding-top: $navbar-height;
	padding: 15px 0;
}

.short-container {
	padding-bottom: 63px;
}

.deeplink-container {
	
	min-height: 520px;
	margin-top: -$navbar-min-height;

	.container-full {
		background-image: url($img-bg-deeplinking);
		background-repeat: no-repeat;
		background-size: cover;
	}

	h2 {
		text-shadow: 0px 3px 4px rgba(0,0,0,0.5);
		margin-bottom: 25px;
	}

	h2 + .col-xs-12 img { margin-bottom: 25px;}

	a { color: $deep-linking-color; }
}

///this overrides "zarpadlity" the containers for non mobile devices
/// locks the containers in 970px   
[data-locked="true"] .container {
	
	@media (min-width: $screen-sm-min) { 
		min-width: $container-md;
	}

	@media (max-width: $screen-md-min) {
		
		min-width: $container-md;
		width: $container-md;
	}
	
	min-width: $container-md;
}

[data-locked="true"] .container-wrapper {
	min-width: $container-md;
}

.consumption-collection-container {
 	padding-bottom: 20px;

	[data-locked="true"]  & {
		padding-bottom: 0;
 	}
}

.deeplink-container {

	.container-full {
		background-image: url($img-bg-itv-landing);
		margin-top: 0;
	}
}
