.label {
    font-size: 90%;
    font-weight: normal;
    display: inline-block;
    border-radius: 15px;
}

.label-default {
    margin-bottom: 5px;
    padding: 5px 10px 5px 0;
    background-color: transparent;
    font-size: 1em;
}

.label-status {
    position: absolute;
    z-index: 9;
    background-color: $bg-item-status;
    border-radius: 0 !important;
    color: $text-color;
    padding: 5px 10px;
    font-size: 0.813em;
    top: 10px;
    right: 5px;

    [data-locked="true"] & {
        right: -5px;

        &:after {
            content: '';
            position: absolute;
            bottom: -5px;
            right: 0;
            background-color: transparent;
            width: 0;
            height: 0;
            border-left: 0;
            border-right: 5px solid transparent;
            border-top: 5px solid $bg-item-status-shadow;
        }
    }
}

.container-detail-mobile {
    .slider-detail {
        .label-status {
            top: inherit;
            bottom: 50px;
        }
    }
}

.tags {
    a {

        &:hover {
            text-decoration: none;
        }

        .label-default {
            padding: 4px 7px 0px 0;
        }
    }
}

.genre-item {
    @media (min-width: $screen-sm-min) {
        width: 110px;
    }

    [data-locked="true"] & {
        width: 129px;
        display: inline-block;
    }
}
